  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  功能名稱
  /// <summary>
  public function_name: string;
  /// <summary>
  ///  功能參數
  /// <summary>
  public xpath: string;
  /// <summary>
  ///  遠端連線編號
  /// <summary>
  public remote_conn_id: string;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  檔案名稱
  /// <summary>
  public filename: string;
  /// <summary>
