import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileHttpHtmlDownFileListResponse } from "../../Model/Response/FileHttpHtmlDownFileListResponse";
import { FileHttpHtmlDownFileResponse } from "../../Model/Response/FileHttpHtmlDownFileResponse";
import { FileHttpHtmlDownFileViewModel } from '../../Model/ViewModel/FileHttpHtmlDownFileViewModel';
import { StringResponse } from "../../Model/Response/StringResponse";

/*
  Generated class for the FileHttpHtmlDownFileServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileHttpHtmlDownFileServicesProvider {
  private FileHttpHtmlDownFiles: FileHttpHtmlDownFileListResponse;
  private FileHttpHtmlDownFile: FileHttpHtmlDownFileResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FileHttpHtmlDownFile";
    console.log("Hello FileHttpHtmlDownFileServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
     sub_url = "FileHttpHtmlDownFile?PageSize={1}&PageNumber={2}&keyword={3}&exec_file_seq={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_file_seq,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileHttpHtmlDownFiles = new FileHttpHtmlDownFileListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileHttpHtmlDownFiles;
    });
  }



  public PostPutAsync(FileHttpHtmlDownFileViewModel:FileHttpHtmlDownFileViewModel,mode:string) {
    var sub_url: string;

    if(mode==="POST")
      sub_url = "FileHttpHtmlDownFile";
    else
    sub_url = "FileHttpHtmlDownFile/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileHttpHtmlDownFileViewModel.export_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileHttpHtmlDownFileViewModel,mode).map(
      item => {
        this.FileHttpHtmlDownFile = new FileHttpHtmlDownFileResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileHttpHtmlDownFile;
      }
    );
  }

  public DeleteAsync(export_seq:number) {
    var sub_url: string;
    sub_url = "FileHttpHtmlDownFile/{1}";
    sub_url = String.Format(sub_url, this.ctl,export_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }

  public GetSqlAsync(export_seq:number,method:string) {
    var sub_url: string;
    sub_url = "FileHttpHtmlDownFile/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,export_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



